@import url(https://fonts.googleapis.com/css2?family=Bangers&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Kalam:wght@400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=VT323&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face {
  font-family: 'CrimeFighter BB';
  src: local('CrimeFighter BB'), url(/static/media/CRIMFBRG.65bff7ad.TTF) format('truetype');
}

@font-face {
  font-family: 'Komika Title';
  src: local('Komika Title'), url(/static/media/KomikaTitle-Paint.0e6b7149.ttf) format('truetype');
}

@font-face {
  font-family: 'Agent Red';
  src: local('Agent Red'), url(/static/media/AGENTRED.14091adb.TTF) format('truetype');
}
* {
  width: auto;
  height: auto;
  padding: 0;
  margin: 0;
}
body {
  background-color: #f9f9f2;
  overflow: scroll;
}
.App {
  overflow-x: hidden;  
}
#container {
  display: flex;  
  margin: 0;
  position: absolute;

}
#container-frames {
  width: 100%;
  /* background-color: aqua; */
  display: flex;
  align-items: flex-start;
}
#title-container {
  display: flex;
  align-items: center;
  align-content: space-between;
  justify-content: space-between;
}

#character-name {
    font-family: 'Komika Title', cursive;
    font-size: 2.5vw;
    margin-top: 6vw;
    margin-bottom: -10vw;
    margin-left: 2vw;
    text-align: center;
    text-shadow: 4px 4px 2px rgb(122, 107, 201);
    color: rgb(232, 62, 32);
    letter-spacing: 0;
    line-height: 3.5vw;
    width: 20%; /* IE 9 */
    transform: rotate(-4deg);
    /* background-color: #e9ce03; */
    background: url(/static/media/pow.6daa3de7.png);
    background-size: 100% 100%;
    background-position: center; 
    height: 12vw;
    padding-top: 3vw;
  }
  #in-div {
    position: relative;
    font-family: 'Komika Title', cursive; 
    font-size: 1.5vw;
    vertical-align: middle;
    margin-top: 8vw;
    margin-bottom: -6vw;
  }
  
  #title {
    font-family: 'Komika Title', cursive; 
    font-size: 6.5vw;
    margin-top: 6vw;
    margin-bottom: -8vw;
    text-align: center;
    color: darkslateblue;
    letter-spacing: -3px;
    width: 60%;
  }
  
  #subtitle {
    font-family: 'Komika Title', cursive; 
    font-size: 2vw;
    font-weight: 100;
    margin-top: 7vw;
    margin-bottom: -8vw;
    margin-right: 2vw;
    text-align: center;
    color: rgb(35, 33, 50);
    width: 20%;
    
  } 
#frame {
    position: relative;
    background-image: linear-gradient(to top, rgb(3, 120, 245), rgb(160, 199, 237));
    width: 30vw;
    height: 30vw;
    margin: 8vw 1vw 1vw 2vw;
    border: 0.5vw rgba(41, 40, 40, 0.94) solid;
    /*border-radius: 0.5%;*/
    border-radius: 255px 1px 225px 4px/13px 225px 9px 255px;
    display: inline-flex;
  }
#avatar-container {
    -webkit-animation: avatar-enters-page 2s ease-in forwards;
      -moz-transition: avatar-enters-page 2s ease-in forwards;
        -o-transition: avatar-enters-page 2s ease-in forwards;
            animation: avatar-enters-page 2s ease-in forwards;
    z-index: 98;
    margin: -4vw 1vw;
  }
  
  #avatar {
    -webkit-animation: move-up-and-down 1s infinite  alternate;
      -moz-transition: move-up-and-down 1s infinite  alternate;
        -o-transition: move-up-and-down 1s infinite  alternate;
            animation: move-up-and-down 1s infinite  alternate;
    border-radius: 100%;
    border: 1.5vw orange solid;
    width: 20vw;
    height: 20vw;
    position: relative;
    margin-top: 10vw;
    z-index: 99;
    box-shadow: 5px 10px 8px #4a4949;
  }
  
  #balloon {
    pointer-events: none;
    animation: baloon-enter-page 2s ease-in forwards;
    height: 20vw;
    width: 58vw;
    margin: -2.8vw -12vw;
    float: right;
    position: relative;
    z-index: 100;
  }
  
  #balloon-text {
    animation: baloon-enter-page 2s ease-in forwards;
    width: 46vw;
    height: 5vw;
    margin-left: -41vw;
    margin-top: 0vw;
    font-size: 2.0vw;
    font-family: 'CrimeFighter BB', cursive;
    z-index: 200;
    text-align: center;
    line-height: 1.3;
  }
  
  @keyframes avatar-enters-page {
    0% {
      transform: translateX(-100%) translateY(-100%);
    }
    100% {
      transform: translate(0);
    }
  }
  
  @keyframes baloon-enter-page {
    0% {
      transform: translateX(200%) translateY(-100%);
    }
    100% {
      transform: translate(0);
    }
  }
  @keyframes move-up-and-down {
    0% { transform: translateY(0); }
    100% { transform: translateY(-5px); }
  }


#bubble-container {
    z-index: 99;
    overflow: hidden;
    height: 30vw;
    width: 30vw;
    position: absolute;
    bottom: 0;
    margin: -4.8vw 0.2vw;
  }
  
  .bubble {
    width: 6vw;
    height: 6vw;
    background: #48a8e4;
    border-radius: 100%;
    -moz-border-radius: 100%;
    -webkit-border-radius: 100%;
    position: absolute;
  }
  
  .bubble1 {
    left: 11.2vw;
    transform: scale(0.6);
    opacity: 0.5;
    -webkit-animation: moveclouds 25s linear infinite, sideWays 2s ease-in-out infinite alternate;
    -moz-animation: moveclouds 25s linear infinite, sideWays 2s ease-in-out infinite alternate;
    -o-animation: moveclouds 25s linear infinite, sideWays 2s ease-in-out infinite alternate;
    position: absolute;
  }
  
  .bubble2 {
    left: 17vw;
    transform: scale(0.4);
    opacity: 0.4;
    -webkit-animation: moveclouds 20s linear infinite, sideWays 4s ease-in-out infinite alternate;
    -moz-animation: moveclouds 20s linear infinite, sideWays 4s ease-in-out infinite alternate;
    -o-animation: moveclouds 20s linear infinite, sideWays 4s ease-in-out infinite alternate;
    position: absolute;
  }
  
  .bubble3 {
    left: 10vw;
    transform: scale(0.7);
    opacity: 0.7;
    -webkit-animation: moveclouds 15s linear infinite, sideWays 3s ease-in-out infinite alternate;
    -moz-animation: moveclouds 15s linear infinite, sideWays 3s ease-in-out infinite alternate;
    -o-animation: moveclouds 15s linear infinite, sideWays 3s ease-in-out infinite alternate;
    position: absolute;
  }
  
  .bubble4 {
    left: 3vw;
    transform: scale(0.5);
    opacity: 0.5;
    -webkit-animation: moveclouds 10s linear infinite, sideWays 4s ease-in-out infinite alternate;
    -moz-animation: moveclouds 10s linear infinite, sideWays 4s ease-in-out infinite alternate;
    -o-animation: moveclouds 10s linear infinite, sideWays 4s ease-in-out infinite alternate;
    position: absolute;
  }
  
  .bubble5 {
    left: 5.5vw;
    transform: scale(0.2);
    opacity: 0.8;
    -webkit-animation: moveclouds 8s linear infinite, sideWays 6s ease-in-out infinite alternate;
    -moz-animation: moveclouds 8s linear infinite, sideWays 6s ease-in-out infinite alternate;
    -o-animation: moveclouds 8s linear infinite, sideWays 6s ease-in-out infinite alternate;
    position: absolute;
  }
  
  .bubble6 {
    left: 4.7vw;
    transform: scale(0.4);
    opacity: 0.7;
    -webkit-animation: moveclouds 6s linear infinite, sideWays 8s ease-in-out infinite alternate;
    -moz-animation: moveclouds 6s linear infinite, sideWays 8s ease-in-out infinite alternate;
    -o-animation: moveclouds 6s linear infinite, sideWays 8s ease-in-out infinite alternate;
    position: absolute;
  }
  
  .bubble7 {
    left: 5.7vw;
    transform: scale(0.35);
    opacity: 0.6;
    -webkit-animation: moveclouds 7.5s linear infinite, sideWays 3s ease-in-out infinite alternate;
    -moz-animation: moveclouds 7.5s linear infinite, sideWays 3s ease-in-out infinite alternate;
    -o-animation: moveclouds 7.5s linear infinite, sideWays 3s ease-in-out infinite alternate;
    position: absolute;
    z-index: 100;
  
  }
  
  @keyframes moveclouds {
    0% { 
        top: 500px;
    }
    100% { 
        top: -500px;
    }
  }
  
  @keyframes sideWays {
    0% {
        margin-left:0px;
    }
    100% {
        margin-left:50px;
    }
  }
