  #game-score {
    font-family: 'VT323', monospace;
    font-size: 5vw;
    color: rgb(34, 28, 140);
    right: 1.5vw;
    top: 0.8vw;
    position: absolute;
  }
  
  #flying-saucer-container {
    z-index: 99;
    overflow: hidden;
    height: 30vw;
    width: 60vw;
    position: absolute;
    bottom: 0;
    margin: -0.5vw;
  }
  
  #wrapping-flying-saucer {
    pointer-events: auto;
    height: 6vw;
    width: 6vw;
    background-size: 100% 100%;
    background-position: center; 
    z-index: 200;
    animation: fs 15s ease-in-out infinite;
  }
  
  #flying-saucer {
    pointer-events: auto;
    position: relative;
    height: 6vw;
    width: 6vw;
    background: url(/static/media/flying-saucer.6a91c450.png);
    background-size: 100% 100%;
    background-position: center; 
    z-index: 200;
    
  }
  
  #flying-saucer:hover {
    pointer-events: auto;
    height: 6vw;
    width: 6vw;
    background: url(/static/media/flying-saucer2.3a8bff4c.png);
    background-size: 100% 100%;
    background-position: center; 
    z-index: 200;
  }
  
  #flying-saucer:hover:active {
    pointer-events: auto;
    height: 6vw;
    width: 6vw;
    background: url(/static/media/flying-saucer.6a91c450.png);
    background-size: 100% 100%;
    background-position: center; 
    z-index: 200;
    animation: shake 0.5s;
    border-radius: 50%;
    box-shadow:
      inset 0 0 50px #fff,      /* inner white */
      inset 20px 0 80px rgb(255, 0, 4),   /* inner left magenta short */
      inset -20px 0 80px #0ff,  /* inner right cyan short */
      inset 20px 0 300px rgb(255, 0, 0),  /* inner left magenta broad */
      inset -20px 0 300px #0ff, /* inner right cyan broad */
      0 0 50px #fff,            /* outer white */
      -10px 0 80px rgb(255, 0, 0),        /* outer left magenta */
      10px 0 80px #0ff;         /* outer right cyan */
  }
  
  @keyframes shake {
    0% { transform: translate(0.1vw, .1vw) rotate(0deg); opacity:1; }
    10% { transform: translate(-0.1vw, -0.2vw) rotate(-1deg); }
    20% { transform: translate(-0.3vw, 0px) rotate(1deg); }
    30% { transform: translate(0.3vw, 0.2vw) rotate(0deg); }
    40% { transform: translate(0.1vw, -0.1vw) rotate(1deg); }
    50% { transform: translate(-0.1vw, 0.2vw) rotate(-1deg); }
    60% { transform: translate(-0.3vw, 0.1vw) rotate(0deg); }
    70% { transform: translate(0.3vw, 0.1vw) rotate(-1deg); }
    80% { transform: translate(-0.1vw, -0.1vw) rotate(1deg); }
    90% { transform: translate(0.1vw, 0.2vw) rotate(0deg); }
    100% { transform: translate(0.1vw, -0.2vw) rotate(-1deg); opacity:0;}
  }
  
  @keyframes fs {
    0% {
      transform: translateX(250%) translateY(250%);
    }
    10% {
      transform: translateX(560%) translateY(150%);
    }
    30% {
      transform: translateX(590%) translateY(250%);
    }
    35% {
      transform: translateX(590%) translateY(350%);
    }
    40% {
      transform: translateX(580%) translateY(320%);
    }
    50% {
      transform: translateX(660%) translateY(240%);
    }
    55% {
      transform: translateX(700%) translateY(310%);
    }
    58% {
      transform: translateX(600%) translateY(310%);
    }
    80% {
      transform: translateX(440%) translateY(370%);
    }
    90% {
      transform: translateX(330%) translateY(280%);
    }
    100% {
      transform: translateX(250%) translateY(250%);
    }
  }
